#board {
	display: flex;
	box-sizing: border-box;
	background-color: blue;
	width: fit-content;
	border-style: solid;
	border-color: black;
	border-width: 2px;

	/* transform: rotate(90deg);
	margin: 30px; */
}

.collumn {
	display: flex;
	flex-direction: column-reverse;
}

.cell {
	box-sizing: border-box;
	border-style: solid;
	border-color: black;
	border-width: 2px;
	border-radius: 20px;
	width: 3em;
	height: 3em;
	background-color: white;
	margin: 1px;
	text-align: center;
}

.y {
	background-color: yellow;
}

.r {
	background-color: red;
}
